.episode-list {
  width:100%;
  flex: 1;
  margin:0;
  padding-left: $baseSpacing;
  font-size: 0.9em;
  &__header {
    background: $background;
    padding: 1em $basePadding;
    font-size: 1.23em;
    font-weight: bold;
    filter: $dropShadow;
    margin-bottom: $baseSpacing;
  }
  %row {
    display: grid;
    grid-template-columns: 65% 20% 12%;
    gap: 10px 10px;
    padding: 1em;
    div:not(:first-child) {
      text-align: center;
    }
  }
  &__table {
    filter: $dropShadow;
    background: $background;
    padding: $basePadding;
  }
  &__row-header {
    @extend %row;
    font-size: 1.1em;
    font-weight: bold;
  }
  &__row {
    cursor: pointer;
    @extend %row;
    border-top: 1px solid #bbb;
    div:first-child {
      color: $colorTheme;
    }
    &:nth-child(even) {
      background: rgba(0, 0, 0, 0.05);
    }
    &:hover {
      background: $colorTheme;
      color: $invertedColor;
      div:first-child {
        color: $invertedColor;
      }
    }
  }
}
