  animation-delay: -.5s;
}

@keyframes spinner {
  0% {
    width: 0;
    height: 0;
    opacity: 0;
    top: 36px;
    left: 36px;
  }

  4.9% {
    width: 0;
    height: 0;
    opacity: 0;
    top: 36px;
    left: 36px;
  }

  5% {
    width: 0;
    height: 0;
    opacity: 1;
    top: 36px;
    left: 36px;
  }

  100% {
